* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
.content {
  height: 100vh;
}
.register-page {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 90vh;
}
/* transaction page */
.filters {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 20px;
  box-shadow: 0 0 3px gray;
}

/* antd icons */
.switch-icon {
  border: 1px solid rgba(0, 0, 0, 0.513);
  border-radius: 5px;
  padding: 10px 10px;
}

.anticon svg {
  font-size: 20px;
  cursor: pointer;
}

.active-icon {
  color: black;
}
.inactive-icon {
  color: gray;
}
